.div-center {
    margin: auto;
}

.mg-top {
    margin-top: 10px;
}

.form-post {
    width: 600px;
}

.form-input {
    margin-bottom: 10px !important;
    border-radius: 5px !important;
}

.form-button {
    float: right;
}

.post-buttons {
    float: right;
    position: absolute;
    top: 10px;
    right: 10px;
}

.post_votes {
    float: right;
    position: absolute;
    bottom: 10px;
    right: 15px;
}

.vote-up {
    margin-right: 5px;

    &:hover {
        color: green !important;
    }
}

.vote-down {
    &:hover {
        color: red !important;
    }
}

.post-card {
    border: 1px solid #80ccff;
    border-radius: 10px;
    margin-bottom: 10px;
    padding: 10px;
}